@import 'variables';

body{
	transition: all 0.5s;
	font-size:16px;
}
div.img{
	position: relative;
	margin: 0 auto;
	opacity: 0;
	transition: opacity 0.6s;
	span{
		position: absolute;
		font-size: 20em;
		font-weight: bold;
		color: white;
		left: 50%;
		transform: translateX(-50%);
	}
	img{
		border-radius: 10px;
		display: block;
		margin: 0 auto;
	}

}


p{
	.boton{
		font-size: $font-size;
		color: white;
		background-color: orange;
		padding: 30px;
		border-radius: 10px;
		display: inline-block;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%,-50%);
		transform-origin: 167% 150%;
		transition: all 0.5s ease;
		&:hover{
			transform: scale(1.40);
			padding-left:100px;
			padding-right: 100px;
			background: darken(orange, 50%);
		}
	}
	a.boton{
		text-decoration: none;
		color: white;
		&:hover{
			color:orange;
			text-decoration:inherit
		}
	}
}
